
html,body {
    position: relative;
    height: 100%;
}
body {
    background: #eee;
}
.swiper-box {
    width: 100%;
    height: 8rem;
    margin: 0 auto;
}
.swiper-item {
    height: 100%;
    text-align: center;
    font-size: 18px ;
    background: #fff;
    /* Center slide text vertically */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}
div.swiper-pagination span{
    background: white;
}
div.productNum{
    position: absolute;
    right: 0.3rem;
    top: 7.1rem;
    z-index: 100;
    font-size: 0.35rem;
    color: white;
    padding: 0.2rem 0rem;
}
div.productTitleInfo{
    text-align: center;
    background: white;
}
div.productTitle{
    font-size: 0.4rem;
    padding: 0.1rem;
    /* background: white; */
}
div.productPrice{
    color: #fe6734;
}
div.productService{
    background: white;
    margin-top: 0.15rem;
}
div.productServiceTitle{
    font-size: 0.45rem;
    padding: 0.3rem;
    color: #444444;
}
div.productServiceList{
    margin:0.1rem 0rem;
    display: -ms-flexbox;
    display: flex;
    overflow: scroll;
}
span.selectedService{
    color:#14CC81;
}
div.productServiceList span{
    font-size: 0.4rem;
    display: block;
    text-align: center;
    margin-left: 0.15rem;
    margin-bottom: 0.1rem;
    width: 2rem;
}
div.productServiceList span span{
    margin-left: 0rem;
}
div.productLocation{
    margin-top: 0.15rem;
    background: white;
    font-size: 0.4rem;
    line-height: 1rem;
}
img.productLocationImg{
    width: 0.3rem;
    padding-left: 0.3rem;
    float: left;
    padding-right: 0.2rem;
    padding-top: 0.25rem;
}
img.productPhoneImg{
    width: 0.4rem;
    float: right;
    padding-right: 0.3rem;
    padding-top: 0.3rem;
}
div.openAppDiv{
    position: fixed;
    bottom: 0px;
    background: #ffffff;
    opacity: 0.8;
    width: 100%;
}
img.imgLogo{
    height: 1rem;
    margin-left: 0.4rem;
    margin-top: 0.2rem;
}
button.btnOpenApp{
    background: #fe6734;
    color: white;
    font-size: 0.3rem;
    border-radius: 0.1rem;
    border: 0px;
    padding: 0.2rem 0.25rem;
    float: right;
    margin: 0.3rem 0.3rem;
}
div.commentDiv{
    font-size: 0.4rem;
    background: white;
    margin-top: 0.15rem;
    margin-bottom: 2rem;
}
div.customComment{
    padding-top: 0.1rem;
    padding-left: 0.2rem;
}
span.allComment{
    padding-left: 0.2rem;
}
span.allScore{
    float: right;
    padding-right: 0.2rem;
    color: #c40000;
}
div.commentTag{
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    border-top: 1px solid #aaa;
    margin-top: 0.1rem;
    padding-top: 0.25rem;
}
div.commentTag span{
    background: #eee;
    padding: 0.1rem;
    border-radius: 0.1rem;
    display:inline-block;
    margin-bottom: 0.2rem;
    margin-right: 0.1rem;
}



